.main_nav{
    position: fixed;
    background: transparent;
    top: 0;
    width: 100%;
    display: grid;
    grid-template-columns:  1fr 1fr ;
    background-color: black;
    justify-content: space-between;
}
.main_nav.active{
    background: aqua;
}

.main-right >ul{
    border:1px solid;
    display:flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
    
}

/* .main-right >ul>li{
    border:1px solid;
    margin-right: 5%;


} */

.main-right >ul>li>a{
    text-transform: capitalize;
    text-decoration: none;
    color: white;
}

.main-left{
  align-items: center;
    border:1px solid;
    
}

.main-left>h1{
    margin-left: 10%;
    color: white;
}

.hamburger{
    display: none;
}

@media (max-width: 998px){

    .main-left>h1{
        margin-top: 10%;
        margin-left: 10%;
        color: white;
    }

     .menu-link>li{
        display:none
    } 
    .hamburger{
        
        display: block;
        text-align: right;
        margin-right: 5%;
        margin-bottom: 5%;
        font-size: 2.5rem;
    }
    .hamburger>a{
        color: white;
    }

}